$(function () {

    // Selectric init
    $('select').selectric({
        arrowButtonMarkup: '<b class="arrow"></b>'
    });

    // Menu
    const menuToggle = $('[data-menu-toggle]'),
        menu = $('#menu');

    menuToggle.on('click', function () {
        menu.toggleClass('is-show');
        $('body').toggleClass('is-fixed');
    });

    $('body').on('click', function (e) {
        if (!menu.is(e.target) && !menuToggle.is(e.target) && menu.has(e.target).length === 0 && menuToggle.has(e.target).length === 0) {
            menu.removeClass('is-show');
            $('body').removeClass('is-fixed');
        }
    });

    // Scroll to form
    $('a[href*="#form"]').click(function () {
        if (location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') && location.hostname == this.hostname) {
            var target = $(this.hash);
            target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
            if (target.length) {
                $('html, body').animate({
                    scrollTop: target.offset().top - 120
                }, 200);

                target.find('form').addClass('is-flick');

                setTimeout(function () {
                    target.find('form').removeClass('is-flick');
                }, 2000);

                return false;
            }
        }
    });
});